<template>
  <div class="autherinfo" v-if="isLoading" v-show="fullWidth > 950">
    <div class="authersummay">
      <div class="topbar">作者</div>
      <router-link :to="{name:'user_info',params:{name:userInfo.loginname}}">
        <img :src="userInfo.avatar_url" alt="">
      </router-link>
    </div>
    <div class="recent_topics">
      <div class="topbar">作者最近主题</div>
      <ul>
        <li v-for="(list,index) in topiclimitby5" :key="index">
          <router-link :to="{name:'article',params:{id:list.id}}">
            {{list.title}}
          </router-link>
        </li>
      </ul>
    </div>
    <div class="recent_replies">
      <div class="topbar">作者最近回复</div>
      <ul>
        <li v-for="(list,index) in replylimitby5" :key="index">
          <router-link :to="{name:'article',params:{id:list.id}}">
            {{list.title}}
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script lang="js">
  export default {

    name: "SlideBar",
    props: ['artName'],
    data() {
      return {
        userInfo: {},
        isLoading: false,
        fullWidth: document.documentElement.clientWidth
      }
    },
    created() {
      this.getData()
      window.addEventListener('resize', this.handleResize)
    },
    computed: {
      topiclimitby5() {
        if (this.userInfo.recent_topics.length > 5) {
          return this.userInfo.recent_topics.slice(0, 5)
        } else {
          return this.userInfo.recent_topics
        }
      },
      replylimitby5() {
        if (this.userInfo.recent_replies.length > 5) {
          return this.userInfo.recent_replies.slice(0, 5)
        } else {
          return this.userInfo.recent_replies
        }
      }
    }
    ,
    methods: {
      getData() {
        this.$http.get(`https://cnodejs.org/api/v1/user/${this.artName}`)
          .then(res => {
            if (res.data.success === true) {
              this.isLoading = true;
              this.userInfo = res.data.data;
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      handleResize (event) {
        this.fullWidth = document.documentElement.clientWidth
      }
    }
  }
</script>

<style lang="scss" scoped>
  .authersummay, .recent_replies, .recent_topics {
    background-color: #fff;
  }


  .autherinfo {
    width: 328px;
    position: fixed;
    right: 10px;
    margin-top: 20px;
  }

  li {
    padding: 3px 0;
  }

  .recent_replies ul, .recent_topics ul {
    margin-top: 0px;
    margin-bottom: 0px;
    list-style: none;
    padding-left: 14px;
  }

  ul a {
    font-size: 12px;
    text-decoration: none;
    color: #778087;
  }

  .topbar {
    padding: 10px;
    background-color: #f6f6f6;
    height: 16px;
    font-size: 12px;
    margin-top: 10px;
  }

  img {
    height: 48px;
    width: 48px;
    border-radius: 3px;
    margin: 10px;
  }

  .loginname {
    width: 100px;
    float: right;
    margin-top: 22px;
    margin-right: 159px;
    font-size: 14px;
  }

  .loginname a {
    text-decoration: none;
    color: #778087;
  }

  .authersummay .topbar {
    margin-top: 0px;
  }

</style>
