<template>
  <div class="article" :class="{rightMargin:fullWidth>=950}">
    <div class="loading" v-if="isLoading">
      <div class="loader">
        <div class="dot"></div>
        <div class="dot"></div>
        <div class="dot"></div>
        <div class="dot"></div>
        <div class="dot"></div>
      </div>
    </div>
    <div class="topic_header" v-else>
      <SlideBar :art-name="artName" />
      <div class="topic_header">
        <div class="topic_title">{{art.title}}</div>
        <ul>
          <li>•发布于 {{art.create_at | formatDate}}</li>
          <li>•作者：{{art.author.loginname}}</li>
          <li>•{{art.visit_count}}次浏览</li>
          <li>•来自{{art | tabFormatter}}</li>
        </ul>
        <div v-html="art.content" class="topic_content"></div>
      </div>
      <div id="reply">
        <div class="top_bar">回复</div>
        <div v-for="(reply,index) in art.replies" :key="index" class="replySec">
          <div class="replyUp">
            <router-link :to="{name:'user_info',params:{name:reply.author.loginname}}">
              <img :src="reply.author.avatar_url" alt="">
            </router-link>
            <router-link :to="{name:'user_info',params:{name:reply.author.loginname}}">
              <span>{{reply.author.loginname}}</span>
            </router-link>
            <span>{{index+1}}楼</span>
            <span v-if="reply.ups.length > 0">☝ {{reply.ups.length}}</span>
            <span v-else></span>
            <span class="replyTime">{{reply.create_at | formatDate}}</span>
          </div>
          <p v-html="reply.content" class="reply_content"></p>
        </div>
      </div>

    </div>
  </div>
</template>

<script lang="js">
  import SlideBar from "@/components/SlideBar";

  export default {
    name: "Article",
    components: {SlideBar},
    data() {
      return {
        isLoading: false,
        art: {},
        artName: '123',
        fullWidth: document.documentElement.clientWidth,
      }
    },
    created() {
      this.isLoading = true
      this.getArticleData()
      window.addEventListener('resize', this.handleResize)
    },
    methods: {
      getArticleData() {
        this.$http.get(`https://cnodejs.org/api/v1/topic/${this.$route.params.id}`)
          .then(res => {
            if (res.data.success === true) {
              this.isLoading = false;
              this.art = res.data.data;
              this.artName = this.art.author.loginname
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      handleResize(event) {
        this.fullWidth = document.documentElement.clientWidth
      }
    },
    watch: {
      '$route'(to, from) {
        this.getArticleData()
      }
    }
  }
</script>

<style>
  @import url('../assets/markdown-github.css');

  .rightMargin {
    margin-right: 325px;
  }

  .top_bar {
    padding: 10px;
    background-color: #f6f6f6;
    height: 16px;
    font-size: 12px;
    margin-top: 10px;
  }

  .article:not(:first-child) {
    margin-right: 340px;
    margin-top: 20px;
  }

  #reply, .topic_header {
    background-color: #fff;
  }

  #reply {
    margin-top: 15px;
  }

  p.reply_content {
    padding: 10px 35px;
  }

  .replyUp span {
    padding: 0 2px;
  }

  #reply img {
    width: 30px;
    height: 30px;
    position: relative;
    bottom: -9px;
  }

  #reply a, #reply span {
    font-size: 13px;
    color: #666;
    text-decoration: none;
  }

  .replySec {
    border-bottom: 1px solid #e5e5e5;
    padding: 0 10px;
  }

  .loading {
    text-align: center;
    padding-top: 300px;
  }

  .replyUp a:nth-of-type(2) {
    margin-left: 0px;
    display: inline-block;
  }

  .topic_header {
    padding: 10px;
  }

  .topic_title {
    font-size: 20px;
    font-weight: bold;
    padding-top: 8px;
  }

  .topic_header ul {
    list-style: none;
    padding: 0px 0px;
    margin: 6px 0px;
  }

  .topic_header li {
    display: inline-block;
    font-size: 12px;
    color: #838383;
    margin: 0 5px;
  }

  .topic_content {
    border-top: 1px solid #e5e5e5;
    padding: 0 10px;
  }

  .markdown-text img {
    width: 92% !important;
    display: block;
  }

  .loading {
    text-align: center;
    padding-top: 300px;
  }

  .loader {
    position: absolute;
    top: 30%;
    left: 25%;
    margin-left: 10%;
    transform: translate3d(-50%, -50%, 0);
  }

  .dot {
    width: 24px;
    height: 24px;
    background: #3ac;
    border-radius: 100%;
    display: inline-block;
    animation: slide 1s infinite;
  }

  .dot:nth-child(1) {
    animation-delay: 0.1s;
    background: #32aacc;
  }

  .dot:nth-child(2) {
    animation-delay: 0.2s;
    background: #64aacc;
  }

  .dot:nth-child(3) {
    animation-delay: 0.3s;
    background: #96aacc;
  }

  .dot:nth-child(4) {
    animation-delay: 0.4s;
    background: #c8aacc;
  }

  .dot:nth-child(5) {
    animation-delay: 0.5s;
    background: #faaacc;
  }

  @-moz-keyframes slide {
    0% {
      transform: scale(1);
    }
    50% {
      opacity: 0.3;
      transform: scale(2);
    }
    100% {
      transform: scale(1);
    }
  }

  @-webkit-keyframes slide {
    0% {
      transform: scale(1);
    }
    50% {
      opacity: 0.3;
      transform: scale(2);
    }
    100% {
      transform: scale(1);
    }
  }

  @-o-keyframes slide {
    0% {
      transform: scale(1);
    }
    50% {
      opacity: 0.3;
      transform: scale(2);
    }
    100% {
      transform: scale(1);
    }
  }

  @keyframes slide {
    0% {
      transform: scale(1);
    }
    50% {
      opacity: 0.3;
      transform: scale(2);
    }
    100% {
      transform: scale(1);
    }
  }
</style>
